const Leader = ({ i, index, img, name, organizer, length, isLast, ...props }) => {
  return (
    <Flex
      borderTop={index && '1px solid'}
      align="flex-start"
      color="white"
      pt={responsive('0.5em', '0.625em')}
      pb={!isLast && responsive('0.5em', '0.625em')}
      {...props}
    >
      <Flex align="center">
        <Box fontSize={responsive('0.75em', '1em')} width="1.5em">{+index + (i ? length + 1 : 1)}.</Box>
        <Box width={responsive('1.875em', '2.75em')} ml="0.75em" mr="0.5em" borderRadius="50%" overflow="hidden">
          <Image src={img} />
        </Box>
      </Flex>
      <Box flex={1} textAlign="left">
        <Text fontWeight="700" fontSize={responsive('0.75em', '0.875em')}>{name}</Text>
        <Text fontSize="0.625em">{organizer}</Text>
      </Box>
    </Flex>
  )
}

 const FadeBox = (props) => <Box.Absolute transition="display 1s" height="2.375em" left="1px" right="1px" {...props} />

const Page = () => {
  const { isMobile } = useResponsive()
  const [typeIndex, setType] = useState(0)
  const [isScrollToEnd, setScrollTopEnd] = useState()
  const scrollRef = useRef(null);
  const { height } = useWindowSize();
  const { y } = useScroll(scrollRef);
  useEffect(() => {
    if (isMobile && scrollRef.current) {
      setScrollTopEnd(scrollRef.current.offsetHeight + y >= scrollRef.current.scrollHeight)
    }
  }, [y, scrollRef, isMobile])
  // console.log(height)
  return (
    <Box textAlign="center">
      <Text.H2>2020年熱門表演作品回顧</Text.H2>
      <Flex flexDir={responsive('column-reverse', 'column')} mb="1em">
        <Flex flexWrap={responsive('wrap', 'auto')} align="center" mt="0.5em" justify="center">
          {!isMobile && <Text fontSize="0.875em">請依類別篩選</Text>}
          {types.map((t, i) => (
            <Box
              width="6.25em"
              px={responsive('0.375em', '0')}
              mx={responsive('0', '0.625em')}
              mt={isMobile && i + 1 > types.length / 2 && '0.625em'}
              key={i}
            >
              <Button.Transparent
                fontSize={responsive('0.9375em', '1em')}
                py="0.125rem"
                height="auto"
                width="100%"
                onClick={() => setType(i)}
                opacity={i === typeIndex ? 1 : 0.5}
              >{t}</Button.Transparent>
            </Box>
          ))}
        </Flex>
        {isMobile ? (
          <Box>
            <Carousel mt="0.75em" charts typeIndex={typeIndex} setType={setType}>
              {types.map((type, i) => (
                <Box key={i}>{type}</Box>
              ))}
            </Carousel>
            <Box position="relative">
              <Box
                ref={scrollRef}
                mt="0.75em"
                border="1px solid"
                py="0.125em"
                px="0.5em"
                height="16.375em"
                overflow="scroll"
                className="leaderboard"
              >
                {data[typeIndex].map(({ name, organizer, img }, i) => (
                  <Leader
                    name={name}
                    // ref={ref}
                    organizer={organizer}
                    img={img}
                    index={i}
                    key={i}
                  />
                ))}
                <FadeBox display={y ? 'block' : 'none'} background="linear-gradient(#000000 9.21%, rgba(0, 0, 0, 0) 100%)" top="1px" />
                <FadeBox display={isScrollToEnd ? 'none' : 'block'} background="linear-gradient(rgba(0, 0, 0, 0) 9.21%, #000000 100%)" bottom="1px" />
              </Box>
            </Box>
          </Box>
        ) : (
          <Box mt="0.75em" border="1px solid" pb="0.75rem">
            <Text fontSize="1.125em" mt="0.5rem">{types[typeIndex]}</Text>
            <Carousel typeIndex={typeIndex} setType={setType} height={height < 625 ? '42.5vh' : 'auto'}  className="leaderboard">
              {data.map((d, k) => {
                const transformData = d.reduce((res, content, i) => {
                  const index = i % (d.length / 2)
                  if (res[index]) {
                    res[index].push(content)
                    return res
                  }
                  res[+index] = [content]
                  return res
                }, [])
                return (
                  <Box position="relative" key={k} overflowY="auto" height="100%">
                    {transformData.map((content, index) => (
                      <Flex key={index}>
                        {content.map(({ name, organizer, img }, i) => (
                          <Leader
                            i={i}
                            name={name}
                            organizer={organizer}
                            img={img}
                            index={index}
                            length={d.length / 2}
                            width="50%"
                            mx="1em"
                            isLast={index + 1 === (d.length / 2)}
                            key={i}
                          />
                        ))}
                      </Flex>
                    ))}
                    {!isMobile && <Box.AbsCenter width="1px" bg="white" top="0.5em" bottom="0" transform="tranlateX(-50%)" />}
                  </Box>
                )}
              )}
            </Carousel>
          </Box>
        )}
      </Flex>
      <Text textAlign={responsive('left', 'center')} fontSize="0.75em">註1：依據2020節目實際銷售總票數排名，包含同節目巡演場次票數，不含節目售票金額。</Text>
    </Box>
  )
}

export default Page
